import { pages } from "projectConfig"
import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsStaticPage"
import { useAllImagesHomeSlider } from "projectGraphqlStaticQueries/images-slider-home"

export default function Offer003Page(props) {
  const allImages = useAllImagesHomeSlider()

  return (
    <>
      <GatsbySeo
        title={pages.special.offer003.title}
        description={pages.special.offer003.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.special.offer003.title,
          description: pages.special.offer003.description,
        }}
      />
      <Layout>
        <article className={"adaptive text"}>
          {allImages.map(item => {
            let imageFluid = item.node.childImageSharp.fluid
            let imageRelativePath = item.node.relativePath
            if (imageRelativePath === "assets/special-offers/carousel/003.jpg")
              return (
                <Img
                  fluid={imageFluid}
                  key={imageRelativePath}
                  className={"mb-4"}
                ></Img>
              )
          })}
          <h1 className={"mb-4"}>Весь донат со скидкой 60%</h1>
          <p>
            <b>Чтобы воспользоваться предложением нужно:</b>
          </p>
          <ol>
            <li>Выбрать любой товар в премиум магазине World of Tanks</li>
            <li>
              Написать в нашу{" "}
              <Link to={"/contacts"}>техническую поддержку</Link>. В обращении
              указать: сервер (RU,EU и т.д.), игровой ник, ссылку на выбранный
              товар
            </li>
          </ol>
          <p>
            <b>Обратите внимание:</b>
          </p>
          <ol>
            <li>
              Минимальная стоимость заказа $50 (с учетом скидки 60%). Допустим
              вы хотите купить набор: "Rheinmetall Skorpion - Supreme"
              стоимостью в $110. Отнимаете от этой цены 60%, получаете итоговую
              стоимость $44. Следовательно вы должны выбрать еще один товар,
              чтобы итоговая стоимость вашего заказа была больше $50
            </li>
            <li>Срок выполнения заказа 12-24 часа</li>
          </ol>
        </article>
      </Layout>
    </>
  )
}
